import React from 'react';
import s from './schedule.module.css';

export default function Schedule() {
  const year = 2024;
  const month = 8; //  (   0)
  const daysInMonth = new Date(year, month + 1, 0).getDate(); //    
  const startDay = new Date(year, month, 1).getDay(); //     

  const emptyDays = Array.from({ length: (startDay === 0 ? 6 : startDay - 1) }, (_, i) => i);
  const days = Array.from({ length: daysInMonth }, (_, i) => i + 1);

  const daysInPrevMonth = new Date(year, month, 0).getDate();
  const prevMonthDays = Array.from({ length: emptyDays.length }, (_, i) => daysInPrevMonth - emptyDays.length + i + 1);

  const events = [
    { date: 1, time: '10:00', topic: '' },
    { date: 3, time: '14:00', topic: '' },
    { date: 5, time: '16:00', topic: '' },
    { date: 7, time: '09:00', topic: '' },
    //    
  ];

  return (
    <div className={s.schedule}>
      <div className={s.scheduleDate}>
        <p className={s.scheduleDateNum}> 2024</p>
      </div>
      <div className={s.scheduleWeeks}>
        <p className={s.scheduleWeek}></p>
        <p className={s.scheduleWeek}></p>
        <p className={s.scheduleWeek}></p>
        <p className={s.scheduleWeek}></p>
        <p className={s.scheduleWeek}></p>
        <p className={s.scheduleWeek}></p>
        <p className={s.scheduleWeek}></p>
      </div>
      <div className={s.scheduleDays}>
        {prevMonthDays.map(day => (
          <div key={`prev-${day}`} className={`${s.scheduleDay} ${s.prevMonthDay}`}>
            <p className={s.day}>{day}</p>
          </div>
        ))}
        {days.map(day => {
          const event = events.find(e => e.date === day);
          return (
            <div key={day} className={s.scheduleDay}>
              <p className={s.day}>{day}</p>
              {event && (
                <div className={s.event}>
                  {event.time} - {event.topic}
                </div>
              )}
            </div>
          );
        })}
      </div>
    </div>
  );
}
